import React, { Component } from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import {
  Paper,
  Grid,
  Chip,
  Avatar,
  LinearProgress
}
from "@material-ui/core";

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#429ef4",
    },
    secondary: {
      main: "#f4b042",
    }
  },
  typography: {
      // Use the system font instead of the default Roboto font.
      fontFamily: "Ubuntu",
      fontSize: 24,
      color:"#fff"
  }
});

export default  class Resume extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <section id="resume">
        <div className="row education">
          <div className="twelve columns header-col">
            <h1><span>Education</span></h1>
            <p>
              {resumeData.eduDescription}
            </p>
          </div>
          <div className="twelve columns main-col uniCardHolder">
            {
              resumeData.education && resumeData.education.map((item)=>{
                return(
                  <Paper className="uniCard" key={item.UniversityName}>
                    <Grid className="uniImg">
                      <img src={"/images/"+item.img} alt={item.UniversityName} style={{marginLeft:'10%', width:'80%', height:'auto'}}/>
                    </Grid>
                    <Grid className="uniTxt">
                      <h3>{item.UniversityName}</h3>
                      <p className="info">
                      {item.specialization}
                      <span>&bull;</span> <em className="date">{item.MonthOfPassing} {item.YearOfPassing}</em></p>
                      <p>
                      {item.Achievements}
                      </p>
                    </Grid>
                  </Paper>
                )
              })
            }
          </div>
        </div>
        <div className="row work">
            <div className="twelve columns header-col">
              <h1><span>Work</span></h1>
              <p>
                {resumeData.workDescription}
              </p>
            </div>
            <div className="twelve columns main-col workCardHolder">
              {
                resumeData.work && resumeData.work.map((item) => {
                  return(
                    <Paper className="workCard" key={item.CompanyName}>
                      <Grid className="workImg">
                        <img src={"/images/"+item.img} alt={item.CompanyName} style={{marginLeft:'10%', width:'80%', height:'auto'}}/>
                      </Grid>
                      <Grid className="workTxt">
                        <h3>{item.CompanyName}</h3>
                        <p className="info">
                        {item.specialization}
                        <span>&bull;</span> <em className="date">{item.MonthOfLeaving} {item.YearOfLeaving}</em></p>
                        <p>
                        {item.Achievements}
                        </p>
                      </Grid>
                    </Paper>
                  )
                })
              }
            </div> 
         </div>

         <div className="row skill">
            <div className="twelve columns header-col">
              <h1><span>Skills</span></h1>
              <p>
                {resumeData.skillsDescription}
              </p>
            </div>
            <div className="twelve columns main-col">
              <div className="bars">
              {
                resumeData.skilltree && resumeData.skilltree.map((tree) => {
                  return(
                    <ExpansionPanel key={tree.treename}>
                      <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                        <p className="twelve columns header-col">
                          {tree.treename}
                        </p>
                      </ExpansionPanelSummary>
                      <ExpansionPanelDetails>                
                        <ul className="skills">
                          {tree.skills && tree.skills.map((item) => {
                            
                            function Subcat(props) {
                              const hasSubcats = props.hasSubcats;
                              const linSubcats = props.linSubcats;
                              if (hasSubcats && !linSubcats) {
                                const listSubskills = item.skillsub.map((subskill) =>
                                  <div className="subSkillHolder" key={subskill.name}>
                                    <MuiThemeProvider theme={theme}>
                                      <div className="five columns main-col skillprogress">
                                        <Grid container alignItems = "stretch" alignContent = "stretch" justify = "flex-end">
                                          <Chip avatar = {subskill.enabled && 
                                            <Avatar> 
                                              <FontAwesomeIcon color = "#fff"
                                                size = "lg"
                                                icon = {
                                                  [subskill.type, subskill.icon]
                                                }
                                              /></Avatar >}
                                              label = {subskill.name}
                                              color = "secondary"
                                              variant = "outlined"
                                              className = "skillChip"
                                              />
                                            </Grid>
                                          </div>
                                          <div className="seven columns main-col skillprogress">
                                            <Grid>
                                              <LinearProgress 
                                                value={subskill.level}
                                                variant="determinate"
                                                color = "secondary"
                                              />
                                            </Grid>
                                          </div>
                                        </MuiThemeProvider>
                                      </div>
                                    );
                                return listSubskills;
                              } else if(hasSubcats && linSubcats){
                                return(
                                  <div className="inlineLeft" key={item.name}>
                                    {item.skillsub && item.skillsub.map((subskill) => {
                                      return(
                                        <div className="subSkillHolderInline" key={subskill.name}>
                                          <MuiThemeProvider theme={theme}>
                                            <Chip avatar = {subskill.enabled && 
                                              <Avatar> 
                                                <FontAwesomeIcon color = "#fff"
                                                  size = "lg"
                                                  icon = {
                                                    [subskill.type, subskill.icon]
                                                  }
                                                /></Avatar >}
                                                label = {subskill.name}
                                                color = "secondary"
                                                variant = "outlined"
                                                className = "skillChip"
                                                />
                                            </MuiThemeProvider>
                                          </div>
                                        )
                                      })}
                                  </div>
                                )
                              } else {
                                return null;
                              }
                            }
                          return(
                            <li className="row skillHolder" key={item.skillname}>
                              <MuiThemeProvider theme={theme} key={item.skillname}>
                                <div className="subSkillHolder">
                                  <div className="three columns main-col skillprogress">
                                    <Grid container justify = "flex-start" >
                                      <Chip
                                        color="primary"
                                        avatar={<Avatar><FontAwesomeIcon color="#fff" size="lg" icon={[item.icontype, item.iconname]}/></Avatar>}
                                        label={item.skillname}
                                        variant="outlined"
                                        className = "skillChip"
                                      />
                                    </Grid>
                                  </div>
                                  <div className="nine columns main-col skillprogress">
                                    <Grid>
                                      <LinearProgress 
                                        value={item.skilllevel}
                                        variant="determinate"
                                        color = "primary"
                                      />
                                    </Grid>
                                  </div>
                                </div>
                                <Subcat key={item.skillname} hasSubcats={item.skillsub_en} linSubcats={item.skillsub_li} />                                
                              </MuiThemeProvider>
                            </li>
                          )
                        })
                      }
                    </ul>       
                  </ExpansionPanelDetails>
                </ExpansionPanel>
                )
              })}
            </div>
          </div>
        </div>
      </section>
    );
  }
}