import React, { Component } from 'react';

import CardMedia from '@material-ui/core/CardMedia';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Modal from '@material-ui/core/Modal';
import Button from '@material-ui/core/Button';
import { Chip } from '@material-ui/core';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import {
  Scrollbars
}
from 'react-custom-scrollbars';

import Card from "./Card/Card.jsx";
import CardHeader from "./Card/CardHeader.jsx";
import CardBody from "./Card/CardBody.jsx";
import CardFooter from "./Card/CardFooter.jsx";



const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#429ef4",
    },
    secondary: {
      main: "#f4b042",
    }
  },
  typography: {
      // Use the system font instead of the default Roboto font.
      fontFamily: "Ubuntu",
      fontSize: 24,
      color:"#fff"
  }
});

const styles = theme => ({
  layout: {
    width: 'auto',
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(1100 + theme.spacing.unit * 3 * 2)]: {
      width: 1100,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  toolbarMain: {
    borderBottom: `1px solid ${theme.palette.grey[300]}`,
  },
  toolbarTitle: {
    flex: 1,
  },
  toolbarSecondary: {
    justifyContent: 'space-between',
  },
  mainFeaturedPost: {
    backgroundColor: theme.palette.grey[800],
    color: theme.palette.common.white,
    marginBottom: theme.spacing.unit * 4,
  },
  mainFeaturedPostContent: {
    padding: `${theme.spacing.unit * 6}px`,
    [theme.breakpoints.up('md')]: {
      paddingRight: 0,
    },
  },
  mainGrid: {
    marginTop: theme.spacing.unit * 3,
  },
  card: {
    display: 'flex',
  },
  cardDetails: {
    flex: 1,
  },
  cardMedia: {
    width: 160,
  },
  markdown: {
    padding: `${theme.spacing.unit * 3}px 0`,
  },
  sidebarAboutBox: {
    padding: theme.spacing.unit * 2,
    backgroundColor: theme.palette.grey[200],
  },
  sidebarSection: {
    marginTop: theme.spacing.unit * 3,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    marginTop: theme.spacing.unit * 8,
    padding: `${theme.spacing.unit * 6}px 0`,
  },
  paper: {
    position: 'absolute',
    width: theme.spacing.unit * 50,
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing.unit * 4,
    outline: 'none',
  },
});
export default class Porfolio extends Component {
  constructor(props, context) {
    super(props, context);
      
    this.clickHandler = this.clickHandler.bind(this);
    this.hideModal = this.hideModal.bind(this);

    this.state = {
      open: null
    };
  }

  clickHandler(e, index) {
    this.setState({
      activeModal: index
    })
  }

  hideModal() {
    this.setState({
      activeModal: null
    })
  }
  render() {
    let blogData = this.props.blogData;
    let portfolioData = this.props.portfolioData;
    return (
      <section id="portfolio">
        <div className="row">
          <div className="six columns">
            <h1>Latest projects</h1>
            <p>Side and University projects</p>
            <Grid container spacing={40} className={styles.cardGrid}>
            {portfolioData && portfolioData.map((item)=>{
            return(
                <Grid item xs={12} md={6} key={item.title} className="blogCard">
                  <Card chart>
                    <CardHeader className="cardImgHolder" color="warning">
                      <CardMedia
                        className="cardImg"
                        image ={item.arturl} // eslint-disable-line max-len
                        title={item.title}
                        />
                    </CardHeader>
                    <CardBody>
                      <Typography component="h2" variant="h5">
                        {item.title}
                      </Typography>
                      <Typography variant="subtitle1" paragraph>
                        {item.subTitle}
                      </Typography>
                      {item.categories && item.categories.map((cat)=>{
                        return(
                          <MuiThemeProvider theme={theme} key={cat.name}>
                            <Chip
                              label={cat.name}
                              color = "secondary"
                              variant = "outlined"
                              className = "skillChip"
                            />
                          </MuiThemeProvider>
                        )
                      })}
                      <Button id={item.id} onClick={e => this.clickHandler(e, item.id)} style={{color:'#f4b042',float:'right'}}>READ MORE</Button>
                    </CardBody>
                    <CardFooter chart>
                      {item.date}
                    </CardFooter>
                  </Card>

                  <Modal
                    id={item.id}
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    open={this.state.activeModal === item.id}
                    onClose={this.hideModal}>
                    <Paper className="blog-modal">
                      <Scrollbars
                        autoHide
                        autoHideTimeout={1000}
                        autoHideDuration={200}>
                        <h1>{item.title}</h1>
                        <p>{item.date} | {item.subTitle}</p>
                        {item.content && item.content.map((comp)=>{
                          if (comp.type === "image") {
                            return(
                              <Grid key={comp.imgurl.substring(0,12)}>
                                <img className="blogImg" src={comp.imgurl} alt={comp.imgurl}/>
                              </Grid>
                            )
                          } else if (comp.type === "header") {
                            return(
                              <Grid key={comp.content.substring(0,8)}>
                                <h2>{comp.content}</h2>
                              </Grid>
                            )
                          } else if (comp.type === "paragraph") {
                            return(
                                <p key={comp.content.substring(0,18)}>{comp.content}</p>
                            )
                          } else if (comp.type === "graph") {
                            return(
                              <Grid key={comp.title.substring(0,12)}>
                                <img className="blogGraph" src={comp.imgurl} alt={comp.imgurl}/>
                                <h3>{comp.title} [{comp.Source}]</h3>
                              </Grid>
                            )
                          }else if (comp.type === "footer") {
                            return(
                              <Grid key={comp.imgurl.substring(0,8)}>
                                <img className="blogFoot" src={comp.imgurl} alt={comp.imgurl}/>
                              </Grid>
                            )
                          }
                          return null;
                        })}
                      </Scrollbars>
                    </Paper>
                  </Modal>
                </Grid>
                )
              })}
            </Grid>
          </div>
          <div className="six columns">
            <h1>Latest articles</h1>
            <p>Blog articles written for work or leisure</p>
              <Grid container spacing={40}>
              {blogData && blogData.map((item)=>{
                return(
                  <Grid item xs={12} md={6} key={item.title} className="blogCard">
                    <Card chart>
                      <CardHeader className="cardImgHolder" color="warning">
                        <CardMedia
                          className="cardImg"
                          image ={item.arturl} // eslint-disable-line max-len
                          title={item.title}
                          />
                      </CardHeader>
                      <CardBody>
                        <Typography component="h2" variant="h5">
                          {item.title}
                        </Typography>
                        <Typography variant="subtitle1" paragraph>
                          {item.subTitle}
                        </Typography>
                        {item.categories && item.categories.map((cat)=>{
                          return(
                            <MuiThemeProvider theme={theme} key={cat.name}>
                              <Chip
                                label={cat.name}
                                color = "secondary"
                                variant = "outlined"
                                className = "skillChip"
                              />
                            </MuiThemeProvider>
                          )
                        })}
                        <Button id={item.id} onClick={e => this.clickHandler(e, item.id)} style={{color:'#f4b042',float:'right'}}>READ MORE</Button>
                      </CardBody>
                      <CardFooter chart>
                        {item.date}
                      </CardFooter>
                    </Card>

                    <Modal
                      id={item.id}
                      aria-labelledby="simple-modal-title"
                      aria-describedby="simple-modal-description"
                      open={this.state.activeModal === item.id}
                      onClose={this.hideModal}>
                      <Paper className="blog-modal">
                        <Scrollbars
                          autoHide
                          autoHideTimeout={1000}
                          autoHideDuration={200}>
                          <h1>{item.title}</h1>
                          <p>{item.date} | {item.subTitle}</p>
                          {item.content && item.content.map((comp)=>{
                            if (comp.type === "image") {
                              return(
                                <Grid key={comp.imgurl.substring(0,12)}>
                                  <img className="blogImg" src={comp.imgurl} alt={comp.imgurl}/>
                                </Grid>
                              )
                            } else if (comp.type === "header") {
                              return(
                                <Grid key={comp.content.substring(0,8)}>
                                  <h2>{comp.content}</h2>
                                </Grid>
                              )
                            } else if (comp.type === "paragraph") {
                              return(
                                  <p key={comp.content.substring(0,18)}>{comp.content}</p>
                              )
                            } else if (comp.type === "graph") {
                              return(
                                <Grid key={comp.title.substring(0,12)}>
                                  <img className="blogGraph" src={comp.imgurl} alt={comp.imgurl}/>
                                  <h3>{comp.title} [{comp.Source}]</h3>
                                </Grid>
                              )
                            }else if (comp.type === "footer") {
                              return(
                                <Grid key={comp.imgurl.substring(0,8)}>
                                  <img className="blogFoot" src={comp.imgurl} alt={comp.imgurl}/>
                                </Grid>
                              )
                            }
                            return null;
                          })}
                        </Scrollbars>
                      </Paper>
                    </Modal>
                  </Grid>
                )
              })}
              </Grid>
          </div>
        </div>
      </section>
    );
  }
}