import React from 'react';
import PropTypes from 'prop-types';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import {Grid } from "@material-ui/core";


const styles = theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    paddingLeft: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
  },

  cssLabel: {
    color: '#429ef4'
  },

  cssOutlinedInput: {
    '&$cssFocused $notchedOutline': {
      borderColor: "#fff",
    }
  },

  cssFocused: {},

  notchedOutline: {
    borderWidth: '1px',
    borderColor: '#429ef4!important',
    focused: {
      "& $notchedOutline": {
        borderColor: "#fff!important"
      }
    },
  },

});

class Contact extends React.Component {
  state = {
    fname: '',
    lname: '',
    ename: '',
    nname: '',
  };

  handleChange = name => (event,value) => {
    if (event.target.value.length <= 1000) {
      this.setState({
        [name]: event.target.value,
      });
    }
  };

  render() {
    const { classes } = this.props;
    // let resumeData = this.props.resumeData;

    return (
      <section id="contact">
          <div className="row section-head">
            <div className="twelve columns">
            <h1>Leave me a message using the form below!</h1>
              <form className="row twelve columns" noValidate autoComplete="on">
                <Grid 
                  container
                  direction = "column"
                  justify = "space-evenly"
                  alignItems = "center"
                  className = "four columns">
                  <TextField
                    id="standard-name"
                    label="First Name"
                    className = "twelve column"
                    value={this.state.fname}
                    onChange={this.handleChange('fname')}
                    margin="normal"
                    variant="outlined"
                    InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                        focused: classes.cssFocused,
                      },
                    }}
                    InputProps={{
                      classes: {
                        root: classes.cssOutlinedInput,
                        focused: classes.cssFocused,
                        notchedOutline: classes.notchedOutline,
                      },
                      inputMode: "numeric"
                    }}
                  />
                  <TextField
                    id="standard-name"
                    label = "Last Name"
                    className = "twelve column"
                    value={this.state.lname}
                    onChange={this.handleChange('lname')}
                    margin="normal"
                    variant="outlined"
                    InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                        focused: classes.cssFocused,
                      },
                    }}
                    InputProps={{
                      classes: {
                        root: classes.cssOutlinedInput,
                        focused: classes.cssFocused,
                        notchedOutline: classes.notchedOutline,
                      },
                      inputMode: "numeric"
                    }}
                  />
                  <TextField
                    id="standard-name"
                    label = "Email"
                    className = "twelve column"
                    value={this.state.ename}
                    onChange={this.handleChange('ename')}
                    margin="normal"
                    variant="outlined"
                    InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                        focused: classes.cssFocused,
                      },
                    }}
                    InputProps={{
                      classes: {
                        root: classes.cssOutlinedInput,
                        focused: classes.cssFocused,
                        notchedOutline: classes.notchedOutline,
                      },
                      inputMode: "numeric"
                    }}
                  />
                </Grid>
                <Grid
                container
                direction = "column"
                justify = "space-between"
                alignItems = "center"
                className = "eight columns">
                <TextField
                  id="standard-name"
                  label="Message"
                  className="twelve column messagebox"
                  value={this.state.nname}
                  onChange={this.handleChange('nname')}
                  margin="normal"
                  variant = "outlined"
                  multiline
                  // rowsMax = "2"
                  InputLabelProps={{
                    classes: {
                      root: classes.cssLabel,
                      focused: classes.cssFocused,
                    },
                  }}
                  InputProps={{
                    maxLength: 12,
                    classes: {
                      root: classes.cssOutlinedInput,
                      focused: classes.cssFocused,
                      notchedOutline: classes.notchedOutline,
                    },
                    inputMode: "numeric"
                  }}
                /> 
             
                </Grid>
                <Grid
                container
                direction = "column"
                justify = "space-between"
                alignItems = "center"
                className = "twelve columns">
                  <Button variant = "outlined"
                  color = "primary"
                  className = "submitButton"
                  type = "submit"
                  label = "login"
                  InputProps = {
                    {
                      classes: {
                        root: classes.cssOutlinedInput,
                        focused: classes.cssFocused,
                        notchedOutline: classes.notchedOutline,
                      }
                    }
                  }
                  >
                    Submit !
                  </Button>
                </Grid>
                </form>
            </div>
      
            {/* <div className="four columns footerContact">
              <span>{resumeData.name}</span>
              <br></br>
              <span>{resumeData.address}</span>
              <br></br>
              <span>{resumeData.website}</span>
            </div>
            <aside className="eigth columns footer-widgets">
              <div className="widget">
                <h4>Linked in :
                  {resumeData.linkedinId}
                </h4>
              </div>
            </aside> */}
          </div>
        </section>
    );
  }
}

Contact.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Contact);