        "type": "paragraph",
        "content": "The main challenge lies in the protection of the connected objects. The IoT being such a vast number of connected devices, it is appealing to crackers (hackers with potential malicious intent) as it can be programmed into an automated cyber-army. As they are unmanned, sometimes not updated or left with default authentication settings, they can be easy targets. There have been scenarios of mass targeting, such as with the Mirai Botnet. It was a series of cyber-attacks including one on a service provider (Dyn) in October 2016 that used malware infected IoT devices throughout the world that led to a massive part of the internet going down, including famous websites such as Netflix, Twitter, Reddit, CNN, the Guardian… (Read more...). But, it was reported that the use of weak passwords, and in some cases the lack of passwords, caused the connected devices to be made vulnerable to cyber-attacks. However, being aware of its challenges, the IoT remains a technology of choice for many operations. With safety measures in place, encryption of the collected data, and controlled access to any of its layers, stable and secure solutions can be built upon the IoT. Many systems are in places today to protect IoT devices, with automation to change passwords, two-factor authentication, frequent security updates…"
