import React, { Component } from 'react';
import Header from './components/Header';
import About from './components/About';
import Resume from './components/Resume';
import Portfolio from './components/Portfolio';
import Contact from './components/Contact';
import Footer from './components/Footer';

import resumeData from './resumeData';
import blogData from './blogContent';
import portfolioData from './portfolioContent';

import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import ReactGA from 'react-ga';


import './fontawesome';

function initializeReactGA() {
  ReactGA.initialize('UA-133759088-1');
  ReactGA.pageview('/homepage');
}

class App extends Component {
  
  state = {
    open: true,
  };
  handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    this.setState({ open: false });
  };
  
  render() {
    return (
      <div className="App">
        <Header resumeData={resumeData}/>
        <About resumeData={resumeData}/>
        <Resume resumeData={resumeData}/>
        <Portfolio resumeData={resumeData} portfolioData={portfolioData} blogData={blogData}/>
        <Contact resumeData={resumeData}/>
        <Footer resumeData={resumeData}/>

        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={this.state.open}
          autoHideDuration={5000}
          onClose={this.handleClose}
          ContentProps={{
            'aria-describedby': 'message-id',
          }}
          className="snackBar"
          message={<span id="message-id">Welcome to syxren.me!</span>}
          action={[
            <IconButton
              key="close"
              aria-label="Close"
              color="inherit"
              className="closeBtn"
              onClick={this.handleClose}>
              <CloseIcon />
            </IconButton>,
          ]}
        />
      </div>
      
    );
  }
}

export default App;