import React, {Component} from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {Grid } from "@material-ui/core";

export default class Header extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <React.Fragment>
        <nav id="nav-wrap">
          <div className="copyText">
             <p>Copyright <FontAwesomeIcon size="lg" icon={["fas","copyright"]}/> 2019 - Simon Renault</p>
          </div>
          <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
          <a className="mobile-btn" href="#hide" title="Hide navigation">Hide navigation</a>
          <ul id="nav" className="nav">
            <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
            <li><a className="smoothscroll" href="#about">About</a></li>
            <li><a className="smoothscroll" href="#resume">Resume</a></li>
            <li><a className="smoothscroll" href="#portfolio">Works</a></li>
            <li><a className="smoothscroll" href="#contact">Contact</a></li>
          </ul>
          <img src={"/images/vi.png"} alt="Logo" />
        </nav>

        <div className="banner" id="home">
          <Grid 
            container
            direction = "column"
            justify = "space-evenly"
            alignItems = "center">
            <Grid 
              container 
              spacing={24}
              direction="row"
              justify="space-evenly"
              alignItems="flex-start"
              className="headerContainer">
              <Grid item xs={1}></Grid>
              <Grid item xs={9}>
                <Grid 
                container
                direction = "column"
                justify = "center"
                style={{height: '100%'}}>
                  <h1 className="responsive-headline">{resumeData.name}</h1>
                  <hr />
                  <h3 className="responsive-undrline">{resumeData.role}</h3>
                </Grid>
              </Grid>
              <Grid item xs={2}
                style={{backgroundColor: 'rgba(0,0,0,0.6)'}}
                className="headerImgs">  
                <Grid 
                container
                direction = "column"
                justify = "center"
                alignItems = "center"
                alignContent = "center"
                className = "social"
                style={{height: '100%'}}>
                {
                  resumeData.socialLinks && resumeData.socialLinks.map(item =>{
                    return(
                      <Grid
                        key={item.name}
                        container
                        direction = "column"
                        justify = "space-evenly"
                        alignItems = "center" >
                        <a href={item.url} className="socialIcons" rel="noopener noreferrer" target="_blank"><FontAwesomeIcon size="5x" icon={["fab",item.className]}/></a>
                      </Grid>
                      )
                    }
                  )
                }
                </Grid>
              </Grid>
              {/* <Grid item xs={12}>  */}
                <Grid 
                  container 
                  direction="row"
                  justify="space-evenly"
                  alignItems="stretch">
                  <p className="scrolldown">
                      <a className="smoothscroll" href="#about"><FontAwesomeIcon size="1x" icon={["fas","angle-down"]}/></a>
                  </p>
                </Grid>
              {/* </Grid> */}
            </Grid>
          </Grid>
        </div>
      </React.Fragment>
    );
  }
}